import css from "./Welcome.module.css"
import logo from "../../assets/images/logo.png"

import { motion } from "framer-motion"
import { useRef } from "react"
import { useNavigate } from "react-router-dom"

export default function Welcome(props: { loggedIn: boolean }) {
    const navigate = useNavigate()
    const { loggedIn } = props

    const cardRef = useRef<HTMLDivElement | null>(null)
    const imgRef = useRef<HTMLImageElement | null>(null)

    let animationFrameId: number

    const handleMouseMove = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        if (!cardRef.current) return
        cancelAnimationFrame(animationFrameId)

        animationFrameId = requestAnimationFrame(() => {
            const card = cardRef.current
            if (!card) return

            const rect = card.getBoundingClientRect()
            const x = e.clientX - rect.left
            const y = e.clientY - rect.top

            const centerX = rect.width / 2
            const centerY = rect.height / 2

            const rotateX = ((y - centerY) / centerY) * -15
            const rotateY = ((x - centerX) / centerX) * 15

            card.style.transform = `rotateX(${rotateX}deg) rotateY(${rotateY}deg) scale(1.05)`
        })

        animationFrameId = requestAnimationFrame(() => {
            const card = cardRef.current
            const image = imgRef.current
            if (!card || !image) return

            const rect = card.getBoundingClientRect()
            const x = e.clientX - rect.left
            const y = e.clientY - rect.top

            const centerX = rect.width / 2
            const centerY = rect.height / 2

            const rotateX = ((y - centerY) / centerY) * -15
            const rotateY = ((x - centerX) / centerX) * 15

            card.style.transform = `rotateX(${rotateX}deg) rotateY(${rotateY}deg) scale(1.05)`

            image.style.transform = `rotateX(${-rotateX / 2}deg) rotateY(${-rotateY / 2}deg) scale(1.02)`
        })
    }

    const resetTransform = () => {
        if (cardRef.current) {
            cardRef.current.style.transform = "rotateX(0deg) rotateY(0deg) scale(1)"
        }
        if (imgRef.current) {
            imgRef.current.style.transform = "rotateX(0deg) rotateY(0deg) scale(1)"
        }
    }

    if (!loggedIn) return

    return <div className="container">
        <div className={css.layout}>
            <div className={css.leftSection}>
                <div className={css.welcomeBox}>
                    <h1>Welcome to</h1>
                    <h2>Karawang Plant Predictive Maintenance</h2>
                    <p>Digital platform for efficient, real-time performance monitoring, evaluation and analysis.</p>
                    <button onClick={() => { navigate('/dashboard/l1') }}>Go to Dashboard</button>
                </div>
            </div>
            <div className={css.rightSection}>
                <div
                    ref={cardRef}
                    className={css.interactiveCard}
                    onMouseMove={handleMouseMove}
                    onMouseLeave={resetTransform}
                >
                    <img
                        ref={imgRef}
                        src={logo}
                        alt="logo"
                        className={css.logoImage}
                    />
                </div>
            </div>
        </div>
    </div>
}
